          <p>- Kích thước: Dài 63 x Sâu 70 x Cao 91 (cm)</p>
          <p>- Chất liệu: Khung gỗ: 100% Gỗ Tần Bì (Ash) Lớp bọc: Vải Hàn Quốc</p>
          <hr>
          <p>Số lượng: </p>
