      <p >"Serena" mang ý nghĩa của sự bình yên và thoải mái. Lần này, Mitssy gửi đến cả nhà BST Serena là sản phẩm chất chứa
        tình cảm của những người luôn hướng đến giá trị giản dị và tìm kiếm cảm giác bình yên trong những hoài niệm xưa cũ.
        BST Serena với thiết kế hướng đến sự tinh giản, thoải mái và phóng khoáng, là sự giao thoa giữa nét cổ điển và hiện
        đại - được thể hiện trong chất liệu gỗ Tần Bì, gam màu sắc trầm ấm, kiểu dáng sang trọng và hoài cổ. BST Serena không
        chỉ đẹp ở thiết kế mà còn được chú trọng vào công năng. Điển hình ở phần lưng tựa với độ nghiêng vừa phải, đệm ngồi
        dày êm giảm thiểu đau lưng và tác động lên cột sống mang đến sự thoải mái, thư giãn tối đa cho chủ nhân khi ngồi
        đọc sách, uống trà hay nghe một bản nhạc yêu thích. Ngoài ra, sản phẩm dễ dàng phối hợp vào nhiều không gian khác
        nhau như phòng khách hoặc phòng ngủ.

      </p>
      <p>Kích thước: Dài 63 x Sâu 70 x Cao 91 (cm)
      </p>
      <p>Giao hàng: từ 3-5 ngày</p>
      <p>Chính sách bảo hành sản phẩm</p>
    </EL-ROW>

    <el-row>
      <div class="title">
        <h2>SẢN PHẨM TIẾP THEO</h2>
        <p>Ghé thăm website của chúng tôi và lựa chọn sản phẩm bạn cần</p>

