import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import IndexPage from '@/components/child/child.index';
import Detail from '@/components/child/child.detail.vue';
import Contact from '@/components/child/child.contact.vue';
import Saleoff from '@/components/child/child.saleoff.vue';
import Furniture from '@/components/child/child.furniture.vue';
import DecorFurniture from '@/components/child/child.deco_furniture.vue';
import News from '@/components/child/child.news.vue';
import {
  homedir
} from 'os';


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Home',
      component: Home,
      children: [{
          path: '/index',
          name: 'IndexPage',
          component: IndexPage
        },
        {
          path: '/chi-tiet',
          name: 'Detail',
          component: Detail,
        },
        {
          path: '/lien-he',
          name: 'Contact',
          component: Contact,
        },
        {
          path: '/khuyen-mai',
          name: 'Saleoff',
          component: Saleoff,
        },
        {
          path: '/noi-that',
          name: 'Furniture',
          component: Furniture,
        },
        {
          path: '/noi-that-trang-tri',
          name: 'DecorFurniture',
          component: DecorFurniture,
        },
        {
          path: '/tin-tuc',
          name: 'News',
          component: News,
        },
      ]
    },
    {
      path: '/*',
      component: IndexPage
    },
  ],
  mode: 'history'
})



