<style lang="scss" scoped>
  #content {
    padding: 5vh 5vw;
    padding-top: 0;
    .img-responsive {
      width: 100%;
      object-fit: cover;
    }
    h4 {
      text-transform: uppercase;
    }
    .breadcrum {
      width: 100%;
      height: 8vh;
      background: #f6f6f6;
      p {
        margin-top: 0;
        line-height: 8vh;
        padding-left: 10px;
      }
    }
    .title {
      text-align: center;
      padding: 5vh;
      h2 {
        color: #6acddd;
        margin-bottom: 0;
      }
    }
  }

</style>

<template>
  <div id="content">
    <el-row>
      <div class="breadcrum">
        <p>TRANG CHỦ /
          <span style="color: #6acddd">TIN TỨC</span>
        </p>
      </div>
      <div class="title">
        <h2>TIN TỨC</h2>
        <p style="padding: 0 25vh">Hiện nay, trên thị trường nội thất được bày bán với nhiều kiểu dáng, mẫu mã đa dạng. Phù hợp với nhiều không gian,
          căn phòng khác nhau cho bạn thỏa sức lựa chọ. Để lựa chọn nội thất vừa đảm bảo được tính thẩm mỹ vừa đảm bảo được
          công năng sử dụng. Lại tiết kiệm được diện tích là điều không hề dễ dàng với các gia đình. Nội thất MITSSY xin
          đưa ra một vài gợi ý để bạn có thể tham khảo.</p>
      </div>
    </el-row>
    <el-row :gutter="20">
      <el-col :md="8" v-for="item in itemList" :key="item.img">
        <div>
          <img class="img-responsive" :src="item.img">
        </div>
        <div>
          <h4>{{item.title}}</h4>
          <p style="color: gray">
            <i class="fas fa-eye"></i> {{item.views}} |
            <i class="far fa-calendar-alt"></i> {{item.createdAt}}</p>
          <p>{{item.content}}</p>
        </div>
      </el-col>
    </el-row>
    <div>
      <el-pagination style="float: right" background layout="prev, pager, next" :total="3">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'News',
    data() {
      return {
        itemList: [{
          title: 'Bí quyết bảo quản nội thất gỗ',
          img: '../../../static/images/news_03.jpg',
          content: 'Đồ gỗ nội thất tạo cho căn phòng bạn vẻ lộng lẫy, nhưng chỉ sau một thời gian, đồ gỗ bắt đầu mất đi độ bóng. . . . ',
          createdAt: '2018/03/03',
          views: 32,
        }, {
          title: 'Bí quyết bảo quản nội thất gỗ',
          img: '../../../static/images/news_05.jpg',
          content: 'Đồ gỗ nội thất tạo cho căn phòng bạn vẻ lộng lẫy, nhưng chỉ sau một thời gian, đồ gỗ bắt đầu mất đi độ bóng. . . . ',
          createdAt: '2018/03/03',
          views: 32,
        }, {
          title: 'TOP 12 SET BÀN GHẾ PHÒNG ĐẸP',
          img: '../../../static/images/news_07.png',
          content: 'Không gian ăn uống đẹp không chỉ góp phần tạo nên tính thẩm mỹ cho ngôi nhà mà còn giúp cho bữa ăn gia đình thêm ngon miệng hơn. Những bộ bàn ghế ăn cao cấp, . . . . ',
          createdAt: '2018/03/03',
          views: 32,
        }, {
          title: 'TIPS TỰ TRANG TRÍ NGÔI NHÀ MƠ ƯỚC ',
          img: '../../../static/images/news_12.jpg',
          content: 'Để trang trí cho một không gian sống như bạn mơ ước sẽ luôn tốn một khoảng thời gian kha khá, đặc biệt là nếu bạn tự bắt tay vào làm.. . . . ',
          createdAt: '2018/03/03',
          views: 32,
        }, {
          title: 'BỐ TRÍ NỘI THẤT CĂN HỘ ĐÚNG PHONG THỦY ',
          img: '../../../static/images/news_13.jpg',
          content: 'Dưới đây là những điểm cần lưu ý khi bố trí, trang trí, bố cục các vật dụng hay nội thất của phòng khách đúng cách theo phong thủy',
          createdAt: '2018/03/03',
          views: 32,
        }, {
          title: 'VỆ SINH BẢO QUẢN NỘI THẤT BẰNG KIM LOẠI',
          img: '../../../static/images/news_14.jpg',
          content: 'Kim loại được dùng khá phổ biến cho các chi tiết của nội thất phòng bếp, nội thất văn phòng, v.v..  như khung và chân của bàn ghế. ',
          createdAt: '2018/03/03',
          views: 32,
        }, ]
      }
    },
  }

</script>

<style scoped>


</style>



