<style lang="scss" scoped>
  #content {
    padding: 5vh 5vw;
    padding-top: 0;
    .breadcrum {
      width: 100%;
      height: 8vh;
      background: #f6f6f6;
      p {
        margin-top: 0;
        line-height: 8vh;
        padding-left: 10px;
      }
    }
    .title {
      text-align: center;
      padding: 15vh;
      h2 {
        color: #6acddd;
        margin-bottom: 0;
      } 

    
    }
    .maps {

      img {
        width: 100%;
        height: 60vh;
        object-fit: cover;
      }
    }
    .left-panel {
      p {
        text-transform: uppercase;
      }
      .el-input {
        margin-bottom: 10px;

      }
    }
  }

</style>

<template>
  <div id="content">
    <el-row>
      <div class="breadcrum">
        <p>TRANG CHỦ /
          <span style="color: #6acddd">LIÊN HỆ</span>
        </p>
      </div>
      <div class="title">
        <h2>LIÊN HỆ</h2>
        <p>Mọi góp ý của bạn sẽ giúp chúng tôi hoàn thiện hơn.</p>
      </div>
      <div class="maps">
        <img src="../../../static/images/maps.png">
      </div>
      <el-row :gutter="20">
        <el-col :md="12" class="left-panel">
          <p>Nếu có thắc mắc hoặc cần trợ giúp, bạn có thể liên hệ với chúng tôi</p>
          <el-input placeholder="Tên khách hàng"></el-input>
          <el-row :gutter="10">
            <el-col :md="12">
              <el-input placeholder="Số điện thoại"></el-input>

            </el-col>
            <el-col :md="12">
              <el-input placeholder="Email khách hàng..."></el-input>
            </el-col>
          </el-row>
          <el-input type="textarea" placeholder="Nhập tin nhắn của bạn..."></el-input>
          <p>Chúng tôi sẽ gọi ngay cho bạn</p>
          <el-button type="primary">GỬI LIÊN HỆ</el-button>
        </el-col>
        <el-col :md="12">
          <p>
            <b>Tên đơn vị:</b> Công Ty TNHH MITSSY</p>
          <p>
            <b>Địa chỉ:</b> 294 Nguyễn Trọng Tuyển, Phường 1, Quận Tân Bình, TP.HCM.</p>
          <p>
            <b>Showroom:</b> 95A1 Trần Quốc Toản, Phường 7, Quận 3 , TP.HCM.</p>
          <p>Nội thất MITSSY mong muốn đem đến những trải nghiệm cuộc sống tuyệt vời nhất cho bạn và gia đình. Phục vụ theo
            nhu cầu cho từng đối tượng khách hàng về thiết kế, lựa chọn kích thước, chất liệu, màu sắc với mức giá thành
            tốt nhất thị trường. Đảm bảo về quy trình sản xuất gia công chuẩn chỉ, chất lượng.</p>
        </el-col>
      </el-row>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'Contact'
  }

</script>

<style scoped>


</style>



